<template>
  <div>
    <br />
    <h3>회원 정보 수정</h3>
    <div id="make">
      <b-container fluid>
        <b-table-simple responsive>
          <b-tr>
            <b-th>아이디</b-th>
            <b-td>
              <b-form-input
                type="text"
                name="userid"
                v-model="u.userid"
                required
              />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>비밀번호</b-th>
            <b-td>
              <b-form-input
                type="password"
                name="userpwd"
                v-model="u.userpwd"
                required
              />
            </b-td>
          </b-tr>
          <tr>
            <b-th>이름</b-th>
            <b-td>
              <b-form-input
                type="text"
                name="username"
                v-model="u.username"
                required
              />
            </b-td>
          </tr>

          <b-tr>
            <b-th>이메일</b-th>
            <b-td>
              <b-form-input
                type="email"
                name="email"
                v-model="u.email"
                required
              />
            </b-td>
          </b-tr>
          <b-tr>
            <b-th>핸드폰번호</b-th>
            <b-td><VuePhoneNumberInput v-model="u.phone"/></b-td>
          </b-tr>
        </b-table-simple>
      </b-container>
    </div>
    <br />
    <b-button variant="dark"
      ><a href="#" @click="update" style="color:white">완료</a></b-button
    >
  </div>
</template>

<script>
import axios from 'axios';
import VuePhoneNumberInput from 'vue-phone-number-input';
import 'vue-phone-number-input/dist/vue-phone-number-input.css';
export default {
  data() {
    return {
      u: {
        userid: '',
        userpwd: '',
        username: '',
        email: '',
        phone: '',
      },
    };
  },
  components: {
    VuePhoneNumberInput,
  },
  methods: {
    update() {
      this.userid = this.$route.params.userid;
      axios
        .post('http://localhost:8080/happyhouse/user/modify', this.u)
        .then((res) => {
          console.log(res.data);
          this.u = res.data;
          this.$router.push('/happyhouse/manage/list');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },

  headers: {
    'content-type': 'application/x-www-form-urlencoded;charset=utf-8',
  },
  created() {
    this.userid = this.$route.params.userid;
    axios
      .get(`http://localhost:8080/happyhouse/user/detail/${this.userid}`)
      .then((res) => {
        console.log(res.data);
        this.u = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style></style>
