<template>
  <div>
    <br />
    <h3>공지사항 상세보기</h3>
    <div id="t">
      <b-table-simple responsive>
        <b-tr>
          <b-th id="h">글번호</b-th>
          <b-td>{{ n.num }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">글제목</b-th>
          <b-td>{{ n.title }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">작성자</b-th>
          <b-td>{{ n.writer }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">작성일</b-th>
          <b-td>{{ n.writedate }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">내용</b-th>
          <b-td style="white-space: pre-line;">{{ n.content }}</b-td>
        </b-tr>
      </b-table-simple>
      <b-button variant="success"
        ><router-link to="/happyhouse/notice/list" style="color:white"
          >목록으로</router-link
        ></b-button
      >

      <!--수정 삭제도 관리자만-->
      <div v-if="isAdmin">
        <b-button variant="warning" id="m"
          ><a href="#" @click="modify" style="color:white"> 수정 </a></b-button
        >
        <b-button variant="dark" id="d"
          ><a href="#" @click="del" style="color:white"> 삭제 </a></b-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      num: 0,
      n: {},
      isAdmin: false,
    };
  },
  created() {
    this.num = this.$route.params.num;
    console.log(this.num);
    axios
      .get(`http://localhost:8080/happyhouse/notice/detail/${this.num}`)
      .then((res) => {
        console.log(res.data);
        this.n = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
    var u = localStorage.getItem('admin');
    if (u == 'true') this.isAdmin = true;
  },
  methods: {
    modify() {
      this.$router.push('/happyhouse/notice/modify/' + this.num);
    },
    del() {
      this.num = this.$route.params.num;
      axios
        .get(`http://localhost:8080/happyhouse/notice/delete/${this.num}`)
        .then((res) => {
          console.log(res.data);
          this.$router.push('/happyhouse/notice/list');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style>
#d,
#m {
  margin-left: 15px;
}
#h {
  background-color: rgb(169, 218, 197);
}
#t {
  width: 900px;
  display: inline-block;
}
</style>
