<template>
  <div>
    <b-card
      id="newscard"
      overlay
      img-src="http://static1.1.sqspcdn.com/static/p/1119256/12384805/1323089816777/panorama+900+x+300+100ppi-4.jpg?asGalleryImage=true&token=g%2FRRQXng57luMMEOttnwiNBsyKg%3D"
      img-alt="Card Image"
      text-variant="white"
      title="NEWS"
      sub-title="검색어를 통해 관련 뉴스를 찾아보세요"
    >
      <b-card-text> </b-card-text>
    </b-card>

    <b-input-group class="mb-2" id="search">
      <b-input-group-prepend is-text>
        <b-icon icon="search"></b-icon>
      </b-input-group-prepend>
      <b-form-input
        type="text"
        v-on:keyup.enter="search"
        v-model="keyword"
        placeholder="검색어를 입력하세요"
      ></b-form-input>
      <b-button id="sbtn" @click="search">검색</b-button>
    </b-input-group>

    <b-table-simple responsive>
      <b-tr>
        <b-th sticky-column style="width:20%">기사 제목</b-th>
        <b-th style="width:50%">내용</b-th>
        <b-th>보러가기(링크)</b-th>
        <b-th>작성일자</b-th>
      </b-tr>
      <b-tr v-for="n in news" :key="n.originallink">
        <b-td v-html="n.title" sticky-column>{{ n.title }}</b-td>
        <b-td v-html="n.description">{{ n.description }}</b-td>
        <b-td><a :href="n.link">뉴스 보러가기</a></b-td>
        <b-td>{{ n.pubDate | parseDate }}</b-td>
      </b-tr>
    </b-table-simple>
  </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment'; // 날짜 바꾸기 위해 필요한 모듈 추가

export default {
  data() {
    return {
      keyword: '부동산',
      news: [],
    };
  },
  methods: {
    search() {
      this.news = [];
      axios
        .get('http://localhost:8080/happyhouse/naver?keyword=' + this.keyword)
        .then(({ data }) => {
          console.log(data.items);
          this.news = data.items;
          // 여기서 items의 값을 바꿔치기 하는 처리해야함! router push말고
        });
    },
  },
  filters: {
    // 날짜 2020-11-20 이런 형식으로 변경해주는 필터 추가
    parseDate: function(prevDate) {
      return moment(prevDate).format('YYYY-MM-DD');
    },
  },
  created() {
    axios
      .get('http://localhost:8080/happyhouse/naver?keyword=' + this.keyword)
      .then(({ data }) => {
        console.log(data);
        this.news = data.items;
      })
      .catch((err) => console.log(err));
  },
};
</script>

<style>
#sbtn {
  margin-left: 20px;
  margin-right: 50px;
}
#search {
  width: 450px;
  float: right;
}
#newscard img {
  height: 300px;
}
#newscard h4 {
  font-size: 70px;
  font-weight: bold;
}
#newscard h6 {
  color: rgb(225, 234, 241) !important;
  background-color: rgb(71, 71, 114);
  display: inline-block;
}
</style>
