<template>
  <div class="here">
    <br />
    <b-card
      id="faqcard"
      overlay
      img-src="https://operations.ashday.com/sites/default/files/styles/1000/public/when-drupal-is-bad-fit.jpg?itok=i6cL6qTT"
      img-alt="Card Image"
      title="	FAQ	"
      sub-title="검색어를 통해 궁금한 점을 해결해보아요"
    >
      <b-card-text> </b-card-text>
    </b-card>
    <b-input-group class="mb-2" id="search">
      <b-input-group-prepend is-text>
        <b-icon icon="search"></b-icon>
      </b-input-group-prepend>
      <b-form-input
        type="text"
        v-model="keyword"
        placeholder="검색어를 입력하세요"
      ></b-form-input>
      <b-button id="sbtn" @click="search">검색</b-button>
    </b-input-group>

    <b-table-simple responsive>
      <b-tr>
        <b-th>번호</b-th>
        <b-th>제목</b-th>
        <b-th>작성자</b-th>
        <b-th>날짜</b-th>
      </b-tr>
      <b-tr v-for="q in qnas" :key="q.qnum">
        <b-td>{{ q.qnum }}</b-td>
        <b-td>
          <router-link :to="'/happyhouse/qna/read/' + q.qnum">{{
            q.qtitle
          }}</router-link>
        </b-td>
        <!-- read 할 때? -->
        <b-td>{{ q.qwriter }}</b-td>
        <b-td>{{ q.qwritedate }}</b-td>
      </b-tr>
    </b-table-simple>
    <b-button variant="dark"
      ><a href="#" @click="list" style="color:white">목록 모두보기</a></b-button
    >
  </div>
</template>

<script>
import axios from 'axios';

export default {
  created() {
    axios
      .get('http://localhost:8080/happyhouse/qna/list')
      .then((res) => {
        // console.log(res);
        this.qnas = res.data;
      })
      .catch((err) => console.log(err));
  },
  data() {
    return {
      keyword: '',
      qnas: [],
    };
  },
  methods: {
    search() {
      axios
        .get(`http://localhost:8080/happyhouse/qna/search/${this.keyword}`)
        .then((res) => {
          console.log(res.data);
          this.qnas = res.data;
          // this.$router.push('/qna/list');
        })
        .catch((err) => console.log(err));
    },
    list() {
      axios
        .get('http://localhost:8080/happyhouse/qna/list')
        .then((res) => {
          // console.log(res);
          this.qnas = res.data;
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>

