<template>
  <div>
    <br />
    <b-card
      id="usercard"
      overlay
      img-src="https://images.unsplash.com/photo-1486312338219-ce68d2c6f44d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1352&q=80"
      img-alt="Card Image"
      title="회원관리"
    >
      <b-card-text> </b-card-text>
    </b-card>
    <b-input-group class="mb-2" id="search">
      <b-input-group-prepend is-text>
        <b-icon icon="search"></b-icon>
      </b-input-group-prepend>
      <b-form-input
        type="text"
        v-model="keyword"
        placeholder="검색어를 입력하세요"
      ></b-form-input>
      <b-button id="sbtn" @click="search">검색</b-button>
    </b-input-group>
    <!-- userid userpwd username email phone joindate-->
    <b-table-simple responsive>
      <b-tr>
        <b-th>아이디</b-th>
        <b-th>비밀번호</b-th>
        <b-th>이름</b-th>
        <b-th>이메일</b-th>
        <b-th>핸드폰번호</b-th>
        <b-th>가입날짜</b-th>
      </b-tr>
      <b-tr v-for="u in users" :key="u.userid">
        <b-td>{{ u.userid }}</b-td>
        <b-td>{{ u.userpwd }}</b-td>
        <b-td>
          <router-link :to="'/happyhouse/manage/read/' + u.userid">{{
            u.username
          }}</router-link>
        </b-td>
        <!-- read 할 때? -->
        <b-td>{{ u.email }}</b-td>
        <b-td>{{ u.phone }}</b-td>
        <b-td>{{ u.joindate }}</b-td>
      </b-tr>
    </b-table-simple>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  created() {
    axios
      .get('http://localhost:8080/happyhouse/user/list')
      .then((res) => {
        this.users = res.data;
      })
      .catch((err) => {
        alert('에러');
        console.log(err);
      });
  },
  data() {
    return {
      keyword: '',
      users: [],
    };
  },
  methods: {
    search() {
      axios
        .get(`http://localhost:8080/happyhouse/user/search/${this.keyword}`)
        .then((res) => {
          console.log(res.data);
          this.users = res.data;
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>

<style>
#sbtn {
  margin-left: 20px;
  margin-right: 50px;
}
#search {
  width: 450px;
  float: right;
}
#usercard img {
  height: 300px;
}
#usercard h4 {
  font-size: 70px;
  font-weight: bold;
}
#usercard h6 {
  color: rgb(225, 234, 241) !important;
  background-color: rgb(22, 65, 82);
  display: inline-block;
}
</style>
