import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import axios from "axios";
import store from "./store";
import VueSession from "vue-session";
import { BootstrapVue, BootstrapVueIcons } from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import * as VueGoogleMaps from "vue2-google-maps";
import "./quasar";

// 새로고침 할 때 마다 실행될것
const localAccessToken = () => {
  const authtoken = localStorage.getItem("auth-token");
  if (!authtoken)
      return;
  axios.defaults.headers.common["auth-token"] = authtoken;
  store.state.isAuthenticated = true;
  store.state.isAdmin = localStorage.getItem("admin");
  // TODO : Backend server에 auth-token으로 유저 정보 받아와서 저장해야함
}
localAccessToken();

Vue.config.productionTip = false;
Vue.use(VueGoogleMaps, {
  load: {
    key: "AIzaSyCfChyPiLuJQ6W1PdawiqgHx8F2NXX5snE", // env파일이 안먹힘,,,!!!!!!!!!!!!!
    libraries: "places"
    //// If you want to set the version, you can do so:
    // v: '3.26',
  }
});
Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);
Vue.use(VueSession, sessionOptions);


var sessionOptions = {
  persist: true
};

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
