<template>
  <div>
    <div>
      <p class="h1 mb-2">
        <b-icon icon="building"></b-icon> 아파트 매매/전월세
      </p>
    </div>

    <!-- 디자인 [진이] 🌹 -->
    <!-- 체크박스 -->
    <b-form-group>
      <b-form-checkbox-group
        id="checkbox-group-2"
        v-model="checkboxSelected"
        name="flavour-2"
      >
        <b-form-checkbox value="deal">아파트 매매</b-form-checkbox>
        <b-form-checkbox value="rent">아파트 전월세</b-form-checkbox>
      </b-form-checkbox-group>
    </b-form-group>
    <!-- 검색 박스 끝 -->

    <!-- 동별 검색 -->
    <div>
      <b-container fluid>
        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default">시/도 : </label>
          </b-col>
          <b-col sm="8">
            <b-form-select
              v-model="sidoSelected"
              class="mb-3"
              @change="onChangeSido"
            >
              <b-select-option disabled value=""
                >Select a group</b-select-option
              >
              <b-select-option
                v-for="option in sidoOptions"
                :value="option.sido_code"
                :key="option.sido_code"
              >
                {{ option.sido_name }}
              </b-select-option>
            </b-form-select>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default">구/군 :</label>
          </b-col>
          <b-col sm="8">
            <b-form-select
              v-model="gugunSelected"
              class="mb-3"
              @change="onChangeGugun"
            >
              <b-select-option disabled value=""
                >Select a group</b-select-option
              >
              <b-select-option
                v-for="option in gugunOptions"
                :value="option.gugun_code"
                :key="option.gugun_code"
              >
                {{ option.gugun_name }}
              </b-select-option>
            </b-form-select>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default" style="width:105px">읍/면/동 :</label>
          </b-col>
          <b-col sm="8">
            <b-form-select
              v-model="dongSelected"
              class="mb-3"
              @change="onChangeDong"
            >
              <b-select-option disabled value=""
                >Select a group</b-select-option
              >
              <b-select-option
                v-for="option in dongOptions"
                :value="{ dong: option.dong, dongcode: option.dongcode }"
                :key="option.dong_code"
              >
                {{ option.dong }}
              </b-select-option>
            </b-form-select>
          </b-col>
        </b-row>
        <!-- 아파트 이름으로 검색 -->
        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-none">아파트 이름 :</label>
          </b-col>
          <b-col sm="8">
            <b-input-group class="mb-2">
              <b-input-group-prepend is-text>
                <b-icon icon="search"></b-icon>
              </b-input-group-prepend>
              <b-form-input
                type="text"
                v-model="aptSearchKeyword"
                v-on:keyup.enter="searchAptByName"
                placeholder="검색어를 입력하세요"
              ></b-form-input>
            </b-input-group>
          </b-col>
        </b-row>
      </b-container>
    </div>
    <b-button variant="dark" @click="searchAptByName">검색</b-button>

    <br />
    <hr />
    <!-- 지도랑 목록 나란히 [진이] 🌹 -->
    <!-- 검색된 매물들도 띄워야지.... -->
    <!-- 클릭 시 zoom되는 것은 정보주면서 clickMarker메소드 실행하면 될거고 -->
    <!-- 이제 상세페이지를 제작하면 됨. -->
    <!-- 그 이후로는 거의 다 비슷하겠지ㅣ,,, -->

    <div>
      <p class="h3 mb-2"><b-icon icon="bar-chart-fill"></b-icon> 검색결과</p>
      <b-row>
        <b-col cols="6" style="overflow: auto; height: 500px; padding: 0% 3%;">
          <b-container fluid id="list">
            <b-card
              style="width: 300px; float:left;  margin-left:30px; margin-bottom:15px;"
              v-for="(i, index) in infoForUser"
              :key="index"
              @click="resultCardClick(i.lat, i.lng)"
            >
              <b-media style="height:150px;">
                <template #aside>
                  <!-- 이미지는 랜덤으로 -->
                  <b-img
                    blank-color="#ccc"
                    :src="srcLink[parseInt(Math.random() * 4)]"
                    width="100px"
                    height="100px"
                    alt="placeholder"
                  ></b-img>
                </template>
                <!-- 여기 나타낼 수 있는 정보들은 필요할 때 얘기하면 사진 줄게염 😉 -->
                <h5 class="mt-0">
                  <b>{{ i.아파트 }}</b>
                </h5>
                <table style="margin-left:15px">
                  <tr>
                    <th>건축년도 :</th>
                    <td>{{ i.건축년도 }}</td>
                  </tr>
                  <tr>
                    <th>면적 :</th>
                    <td>{{ i.전용면적 }}㎡</td>
                  </tr>
                  <tr>
                    <th>월세 :</th>
                    <td>{{ i.월세금액 }}</td>
                  </tr>
                  <tr>
                    <th>층 :</th>
                    <td>{{ i.층 }}</td>
                  </tr>
                </table>
              </b-media>
            </b-card>
          </b-container>
        </b-col>
        <b-col>
          <!-- 구글 지도 시작 -->
          <div id="map">
            <GmapMap
              :center="{
                lat: center.lat,
                lng: center.lng,
              }"
              :zoom="center.zoom"
              style="height: 500px;"
            >
              <GmapMarker
                v-for="m in markerInfo"
                :key="m.idx"
                :position="m.position"
                :clickable="true"
                @click="
                  clickMarker(m);
                  openWindow(m);
                "
              />
              <!-- 구글 맵 마커 클릭시 보이는 윈도우 창 -->
              <GmapInfoWindow
                @closeclick="markerWindowInfo.window_open = false"
                :opened="markerWindowInfo.window_open"
                :position="markerWindowInfo.infowindow"
                :options="{
                  pixelOffset: {
                    width: 0,
                    height: -35,
                  },
                }"
              >
                <b-card
                  no-body
                  style="max-width: 20rem;"
                  :img-src="srcLink[parseInt(Math.random() * 4)]"
                  img-alt="Image"
                  img-top
                >
                  <template #header>
                    <h4 class="mb-0">
                      {{ markerWindowInfo.info_marker.name }}
                    </h4>
                  </template>
                  <!-- if-else처리해야함. 매매정보랑 전월세 정보가 달라서 정보 값이 undefined면 안보이는 형태로 -->
                  <b-list-group flush>
                    <b-list-group-item
                      >면적 :
                      {{
                        markerWindowInfo.info_marker.area
                      }}㎡</b-list-group-item
                    >
                    <b-list-group-item
                      >건축년도 :
                      {{
                        markerWindowInfo.info_marker.buildYear
                      }}년</b-list-group-item
                    >
                    <b-list-group-item
                      >층수 :
                      {{
                        markerWindowInfo.info_marker.floor
                      }}층</b-list-group-item
                    >
                    <b-list-group-item
                      >보증금 : {{ markerWindowInfo.info_marker.deposit }}만원
                    </b-list-group-item>
                    <b-list-group-item
                      >전월세 :
                      {{ markerWindowInfo.info_marker.monthlyPay }}만원
                    </b-list-group-item>
                  </b-list-group>

                  <!-- 상세페이지 모달 🌹 -->
                  <b-button
                    id="show-btn"
                    @click="
                      checkFavorite();
                      $bvModal.show('bv-modal-example');
                    "
                    >상세보기</b-button
                  >
                  <b-modal id="bv-modal-example" ref="detailModal" hide-footer>
                    <template #modal-title>
                      상세 페이지
                    </template>
                    <b-card
                      no-body
                      style="max-width: 20rem; margin-left:70px"
                      :img-src="srcLink[parseInt(Math.random() * 4)]"
                      img-alt="Image"
                      img-top
                    ></b-card>
                    <table style="width:500px; margin-left:70px">
                      <!-- type에 따라서 다른 정보를 뿌려주는거야... -->
                      <tr>
                        <th>주소 :</th>
                        <td>{{ markerWindowInfo.info_marker.address }}</td>
                      </tr>
                      <tr>
                        <th>방향 :</th>
                        <td>남향</td>
                      </tr>
                    </table>

                    <div v-if="markerWindowInfo.info_marker.favorite">
                      <b-button
                        class="mt-3"
                        variant="outline-danger"
                        block
                        @click="deleteFavorite"
                        >찜 목록에서 삭제</b-button
                      >
                    </div>
                    <div v-if="markerWindowInfo.info_marker.favorite == false">
                      <b-button
                        class="mt-3"
                        variant="outline-danger"
                        block
                        @click="
                          loginCheck();
                          addFavorite();
                        "
                        >💕 찜하기 💕</b-button
                      >
                    </div>
                    <b-button
                      class="mt-3"
                      block
                      @click="$bvModal.hide('bv-modal-example')"
                      >Close</b-button
                    >
                  </b-modal>
                </b-card>
              </GmapInfoWindow>
            </GmapMap>
          </div>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import axios from 'axios';

export default {
  name: 'AptDealRent',
  data() {
    return {
      srcLink: [
        'https://dimg.donga.com/wps/NEWS/IMAGE/2020/11/23/104113818.1.jpg',
        'https://images.unsplash.com/photo-1512917774080-9991f1c4c750?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80',
        'https://images.unsplash.com/photo-1602517263263-730e18a201ba?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=967&q=80',
        'https://images.unsplash.com/photo-1597335590872-782da3d5f1c1?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2024&q=80',
      ],
      // 구글맵에 찍을 초기 설정값
      center: {
        lat: 37.5665734,
        lng: 126.978179,
        zoom: 13,
      },
      checkboxSelected: ['deal', 'rent'], // 매매 * 전월세 검색 선택 데이터
      deal_ymd: '202001', // 계약년월
      // 동별 검색을 위한 데이터들
      sidoSelected: '', // 선택된 시도 코드
      sidoOptions: [],
      gugunSelected: '', // 선택된 구군 코드
      gugunOptions: [],
      dongSelected: '',
      dongOptions: [],
      // 아파트 이름 검색을 위한 데이터
      aptSearchKeyword: '', // 아파트 검색 키워드
      // 응답 데이터를 담을 객체들
      deal: [], // api에서 가져온 아파트 매매 정보들이 담길 배열 객체
      rent: [], // 아파트 전월세 정보들이 담길 배열 객체
      infoForUser: [], // 사용자가 원하는 동의 거래내역 정보들을 담을 배열 객체
      markerInfo: [], // 지도에 표시할 마커들의 정보를 담는 배열 객체
      aptSearchResult: [], // 아파트 이름 검색했을 때 아파트의 정보가 담기는 배열 객체
      // 마커의 윈도우를 띄우기 위한 정보를 담는 객체
      markerWindowInfo: {
        infowindow: { lat: 0, lng: 0 },
        window_open: false,
        info_marker: {},
      },
      latelyIdx: 0, // 최근항목을 위한 인덱스값
    };
  },
  created() {
    // 페이지 생성시 sido정보를 가져온다
    http.get('/map/getSido').then(({ data }) => {
      this.sidoOptions = data;
    });
  },
  methods: {
    // sido 변경 or 선택 시, 그에 맞는 gugun 코드 가져옴
    onChangeSido() {
      http
        .get('/map/getGugun', {
          params: { sido_code: this.sidoSelected },
        })
        .then(({ data }) => {
          this.gugunOptions = data;
        });
    },
    // gugun 변경 or 선택 시,
    onChangeGugun() {
      // 1. 그에 맞는 dong 코드를 가져와야함
      http
        .get('/map/getDong', {
          params: { gugun_code: this.gugunSelected },
        })
        .then(({ data }) => {
          this.dongOptions = [];
          data.forEach((value) => {
            this.dongOptions.push({
              dong: value.dong,
              dongcode: value.dongcode,
            });
          });
        });
      // 2. 공공 api로 거래내역을 가져와야함
      // << 아파트 매매  >>
      // 2-1. 공공 api를 위한 필수 데이터 3가지 : 지역코드(구군), 계약년월, 인증키 / 요청url
      const API_KEY =
        'B9GIa5k7sMsoxX%2FvPFWfQzPNqSupNqibyOCkmPZ3Y9jnJvZZBWD3gp27%2FxOZY0wcTwzAgIRUudp39z4hIIRv%2Fg%3D%3D';
      const API_URL_DEAL =
        'http://openapi.molit.go.kr:8081/OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcAptTradeDev?LAWD_CD=' +
        this.gugunSelected.toString() +
        '&DEAL_YMD=' +
        this.deal_ymd +
        '&ServiceKey=' +
        API_KEY +
        '&numOfRows=1000';
      // 2-2. 데이터 요청으로 원하는 시도, 구군의 거래내역을 가져옴
      axios
        .get(API_URL_DEAL)
        .then((response) => {
          console.log('아파트 매매 >>>');
          console.log(response.data);
          console.log(response.data.response.body.items.item);
          this.deal = response.data.response.body.items.item;
        })
        .catch((error) => {
          console.log(error);
        });
      // << 아파트 전월세  >>
      // 2-1. 공공 api를 위한 필수 데이터 3가지 : 지역코드(구군), 계약년월, 인증키 / 요청url
      var API_URL_RENT =
        'http://openapi.molit.go.kr:8081/OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcAptRent?LAWD_CD=' +
        this.gugunSelected.toString() +
        '&DEAL_YMD=' +
        this.deal_ymd +
        '&serviceKey=' +
        API_KEY +
        '&numOfRows=1000'; // 1000개의 정보
      // 2-2. 데이터 요청으로 원하는 시도, 구군의 거래내역을 가져옴
      axios
        .get(API_URL_RENT)
        .then((response) => {
          console.log('아파트 전월세 >>>');
          console.log(response.data.response.body.items.item);
          this.rent = response.data.response.body.items.item;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // 사용자가 dong 변경 or 선택 시, 받은 데이터에서 동코드와 일치하는 코드들을 뿌려줌
    // 사용자가 체크박스를 선택한 것에 따라서 검색 결과를 다르게 보여줌.
    onChangeDong() {
      var chBoxLen = this.checkboxSelected.length;
      if (chBoxLen == 0) {
        console.log('체크된것 없음');
        alert('검색 종류를 선택해주세요!');
        this.sidoSelected = '';
        this.gugunSelected = '';
        this.dongSelected = '';
      } else {
        this.infoForUser = [];
        for (var i = 0; i < chBoxLen; i++) {
          if (this.checkboxSelected[i] == 'deal') {
            console.log('매매 실행이 된디');
            // deal 매매 선택시
            this.deal.forEach((value) => {
              if (value.법정동읍면동코드 == this.dongCode) {
                this.infoForUser.push(value);
              }
            });
          } else {
            console.log('전월세 실행이 된디');
            // rent 전월세 선택시
            this.rent.forEach((value) => {
              if (value.법정동.replace(' ', '') == this.dongSelected.dong) {
                this.infoForUser.push(value);
              }
            });
          }
        }
      }
      console.log(this.infoForUser); // 없으면 거래내역이 없습니다 라고 해주기 😍😊😘🌹😎
      this.getPosition(this.infoForUser);
    },
    // 저 읍면동 안에 있는 아파트 이름으로 정보 검색 😉
    searchAptByName() {
      this.aptSearchResult = [];
      this.infoForUser.forEach((value) => {
        if (value.아파트.indexOf(this.aptSearchKeyword) != -1) {
          this.aptSearchResult.push(value);
        }
      });
      this.infoForUser = [];
      this.infoForUser = this.aptSearchResult;
      this.getPosition(this.infoForUser);
    },
    //////////// infoForUser를 통해서 구글맵 위치 정보 뽑아오기 ////////////
    getPosition(data) {
      this.markerInfo = [];
      data.forEach((value, index) => {
        var gMapApiKey = 'AIzaSyCfChyPiLuJQ6W1PdawiqgHx8F2NXX5snE';
        var address = value.법정동.concat(' ', value.아파트, ' ', value.지번); // 주소를 가지고
        let tmpLat;
        let tmpLng;
        let u;
        axios
          .get(
            `https://maps.googleapis.com/maps/api/geocode/json?address=${address}&key=${gMapApiKey}` // api요청을 통해
          )
          .then((response) => {
            // 위치값을 받아온다.
            tmpLat = response.data.results[0].geometry.location.lat;
            tmpLng = response.data.results[0].geometry.location.lng;
            // dealForUser의 정보들을 markerInfo에 넣어주고
            // (필요한 정보들 : 거래금액, 년, 월, 일, 아파트(이게 아파트이름), 전용면적, 건축년도)
            u = localStorage.getItem('user-id');
            this.markerInfo.push({
              idx: index,
              position: {
                lat: tmpLat,
                lng: tmpLng,
              },
              amount: value.거래금액,
              date: value.년 + '-' + value.월 + '-' + value.일,
              name: value.아파트,
              buildYear: value.건축년도,
              area: value.전용면적,
              deposit: value.보증금액,
              monthlyPay: value.월세금액,
              floor: value.층,
              userid: u,
              address: address,
              favorite: false,
            });
            // 반대로도 좌표정보를 infoForUser에 넣어준다... 둘을 연결하기 위해서 그나마 생각한 방식임,,,,
            value.lat = tmpLat; // infoForUser에서는 position 객체 생성없이 넣어줌 😎
            value.lng = tmpLng;
          })
          .catch((err) => console.log(err));
      });
      console.log('마커인포>>>>');
      console.log(this.markerInfo);
    },
    ////////////// 마커 클릭 이벤트를 처리하기 위한 메소드들 ////////////////
    clickMarker(m) {
      this.center.lat = m.position.lat;
      this.center.lng = m.position.lng;
      this.center.zoom = 17;
      // 마커의 정보를 로컬스토리지에 저장한다. => 최근 본 항목들을 Home에 뿌려주기 위함임!
      var objName = `lately${this.latelyIdx}`;
      console.log(objName);
      localStorage.setItem(objName, JSON.stringify(m));
      this.latelyIdx += 1;
    },
    ////////// 마커 클릭시 창(말풍선)을 띄우기 위한 메소드 ////////////
    openWindow(m) {
      this.markerWindowInfo.window_open = true;
      this.markerWindowInfo.infowindow.lat = m.position.lat; // 여기도 이렇게 안줘도 되네,, 일단 나중으로,,
      this.markerWindowInfo.infowindow.lng = m.position.lng;
      this.markerWindowInfo.info_marker = m;
    },
    resultCardClick(latitude, longitude) {
      this.center.lat = latitude;
      this.center.lng = longitude;
      this.center.zoom = 18;
    },
    //////////////////////// 찜하기 관련 ///////////////////////
    addFavorite() {
      console.log(this.markerWindowInfo.info_marker);
      http
        .post('/favorite/add', this.markerWindowInfo.info_marker)
        .then(({ data }) => {
          if (data == 'add-success') {
            alert('찜 목록에 추가되었습니다.');
            this.$refs.detailModal.hide();
          } else {
            alert('추가하는데 오류가 발생했습니다.');
          }
        });
    },
    checkFavorite() {
      http
        .post('/favorite/checkFavorites', this.markerWindowInfo.info_marker)
        .then(({ data }) => {
          console.log(data);
          // data true이면
          if (data) {
            this.markerWindowInfo.info_marker.favorite = true;
          } else {
            this.markerWindowInfo.info_marker.favorite = false;
          }
        })
        .catch((err) => console.log(err));
    },
    deleteFavorite() {
      http
        .post('/favorite/delete', this.markerWindowInfo.info_marker)
        .then(({ data }) => {
          if (data == 1) {
            alert('찜 목록에서 삭제했습니다!');
            this.$refs['detailModal'].hide();
          } else {
            alert('삭제하는데 문제가 발생했습니다!');
            this.$refs['detailModal'].hide();
          }
        })
        .catch((err) => console.log(err));
    },
    loginCheck() {
      if (localStorage.getItem('user-id') == '') {
        alert('로그인이 필요한 기능입니다.');
        this.$router.push('/happyhouse');
      }
    },
  },
};
</script>

<style>
.active {
  background: green;
}
</style>
