<template>
  <div>
    <br />
    <h3>FAQ 수정</h3>
    <div id="make">
      <b-container fluid>
        <b-form @submit.prevent="update">
          <b-row class="my-1">
            <b-col sm="2">
              <label for="input-default">제목: </label>
            </b-col>
            <b-col sm="10">
              <b-form-input
                id="input-default"
                v-model="qna.qtitle"
                required
              ></b-form-input>
            </b-col>
          </b-row>

          <b-row class="my-1">
            <b-col sm="2">
              <label for="input-default">내용:</label>
            </b-col>
            <b-col sm="10">
              <b-form-textarea
                id="ta"
                v-model="qna.qcontent"
                debounce="500"
                rows="10"
                max-rows="100"
                required
              ></b-form-textarea>
              <pre class="mt-2 mb-0"></pre>
            </b-col>
          </b-row>
          <br />

          <b-button type="submit" variant="dark" style="color:white;">
            완료
          </b-button>
        </b-form>
      </b-container>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      qnum: 0,
      qna: {},
    };
  },
  methods: {
    update() {
      this.qnum = this.$route.params.qnum;
      console.log(this.qna);
      axios
        .post('http://localhost:8080/happyhouse/qna/update', this.qna)
        .then((res) => {
          console.log(res.data);
          this.qna = res.data;
          this.$router.push('/happyhouse/qna/list');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.qnum = this.$route.params.qnum;
    axios
      .get(`http://localhost:8080/happyhouse/qna/detail/${this.qnum}`) // http://localhost:8080/happyhouse/qna/detail/읽으려는번호
      .then((res) => {
        console.log(res.data);
        this.qna = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style>
#make {
  display: inline-block;
}
#ta {
  width: 500px;
}
#input-default {
  width: 500px;
}
</style>
