<template>
  <div>
    <br />
    <h3>회원 상세보기</h3>
    <div id="t">
      <b-table-simple responsive>
        <b-tr>
          <b-th id="h">아이디</b-th>
          <b-td>{{ user.userid }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">비밀번호</b-th>
          <b-td>{{ user.userpwd }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">이름</b-th>
          <b-td>{{ user.username }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">이메일</b-th>
          <b-td>{{ user.email }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">핸드폰번호</b-th>
          <b-td>{{ user.phone }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">가입날짜</b-th>
          <b-td>{{ user.joindate }}</b-td>
        </b-tr>
      </b-table-simple>
      <b-button variant="warning"
        ><a href="#" @click="modify" style="color:white"> 수정 </a></b-button
      >
      <b-button variant="dark" id="d"
        ><a href="#" @click="del" style="color:white"> 삭제 </a></b-button
      >
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      user: {},
    };
  },
  created() {
    this.userid = this.$route.params.userid;
    axios
      .get(`http://localhost:8080/happyhouse/user/detail/${this.userid}`)
      .then((res) => {
        console.log(res.data);
        this.user = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    modify() {
      this.$router.push('/manage/modify/' + this.userid);
    },
    del() {
      this.userid = this.$route.params.userid;
      axios
        .get(`http://localhost:8080/happyhouse/user/delete/${this.userid}`)
        .then((res) => {
          console.log(res.data);
          this.$router.push('/happyhouse/manage/list');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style>
#d {
  margin-left: 15px;
}
#h {
  background-color: rgb(169, 218, 197);
}
#t {
  width: 900px;
  display: inline-block;
}
</style>
