<template>
  <div>
    <!-- input -->
    <!-- 시/도 -->
    <b-container fluid>
      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-default">시/도 : </label>
        </b-col>
        <b-col sm="8">
          <b-form-select
            v-model="sidoSelected"
            class="mb-3"
            @change="onChangeSido"
          >
            <b-select-option disabled value="">Select a group</b-select-option>
            <b-select-option
              v-for="option in sidoOptions"
              v-bind:value="{
                sido_name: option.sido_name,
                sido_code: option.sido_code,
              }"
              v-bind:key="option.sido_code"
            >
              {{ option.sido_name }}
            </b-select-option>
          </b-form-select>
        </b-col>
      </b-row>

      <!-- 구/군 -->
      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-default">구/군 :</label>
        </b-col>
        <b-col sm="8">
          <b-form-select
            v-model="gugunSelected"
            class="mb-3"
            @change="onChangeGugun"
          >
            <b-select-option disabled value="">Select a group</b-select-option>
            <b-select-option
              v-for="option in gugunOptions"
              v-bind:value="{
                gugun_name: option.gugun_name,
                gugun_code: option.gugun_code,
              }"
              v-bind:key="option.gugun_code"
            >
              {{ option.gugun_name }}
            </b-select-option>
          </b-form-select>
        </b-col>
      </b-row>
    </b-container>
    <br />
    <b-button variant="dark" v-on:click="searchCovid" style="margin-right:15px"
      >선별진료소 검색</b-button
    >
    <b-button variant="dark" v-on:click="searchHospital"
      >국민안심병원 검색</b-button
    >
    <br />
    <hr />
    <br />
    <div>
      <b-row>
        <b-col cols="6" style="overflow: auto; height: 500px; padding: 0% 5%;">
          <b-card
            no-body
            class="overflow-hidden"
            v-for="r in result"
            :key="r.phone"
            style=" margin-bottom:15px;max-width: 540px;"
          >
            <b-row no-gutters>
              <b-col md="4">
                <b-card-img
                  :src="srcLink[parseInt(Math.random() * 4)]"
                  alt="Image"
                  class="rounded-0"
                  width="180px"
                  height="180px"
                ></b-card-img>
              </b-col>
              <b-col md="8">
                <b-card-title>{{ r.hospital_name }}</b-card-title>
                <b-card-body>
                  <b-card-text>
                    <table style="margin-left:15px; ">
                      <tr>
                        <th>✔ 시/도 :</th>
                        <td>{{ r.city }}</td>
                      </tr>
                      <tr>
                        <th>✔ 구/군 :</th>
                        <td>{{ r.gugun }}</td>
                      </tr>
                      <tr>
                        <th>✔ 전화번호 :</th>
                        <td>{{ r.phone }}</td>
                      </tr>
                      <tr>
                        <th>✔ 주소 :</th>
                        <td>{{ r.address }}</td>
                      </tr>
                    </table>
                  </b-card-text>
                </b-card-body>
              </b-col>
            </b-row>
          </b-card>
        </b-col>
        <b-col>
          <!-- 구글 지도 시작 -->
          <GmapMap
            :center="{
              lat: center.lat,
              lng: center.lng,
            }"
            :zoom="center.zoom"
            style="width: 800px; height: 500px; margin: auto;"
          >
            <GmapMarker
              v-for="m in markerInfo"
              :key="m.idx"
              :position="m.position"
              :clickable="true"
              @click="
                clickMarker(m);
                openWindow(m);
              "
            />
          </GmapMap>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import axios from 'axios';

export default {
  data() {
    return {
      // 구글맵에 찍을 초기 설정값
      center: {
        lat: 37.5665734,
        lng: 126.978179,
        zoom: 13,
      },
      // 병원 옵션 선택
      selected: [],
      options: [
        { text: '국민안심병원', value: 'A0' },
        { text: '코로나검사 실시기관', value: '97' },
        { text: '코로나 선별진료소 운영기관', value: '99' },
      ],
      sidoSelected: '', // 선택된 시도 코드
      sidoOptions: [],
      gugunSelected: '', // 선택된 구군 코드
      gugunOptions: [],
      result: [],
      markerInfo: [],
      // 이미지
      srcLink: [
        'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRVymwJpzmb2bY5XAkB5oNJKM2gYYXPoy8y-g&usqp=CAU',
        'https://storage.iseverance.com/contentsobj_2013/%EC%8B%A0%EC%B6%95%EC%9A%A9%EC%9D%B8%EB%A0%8C%EB%8D%94%EC%9D%B4%EB%AF%B8%EC%A7%80.jpg',
        'https://pds.medicaltimes.com/NewsPhoto/201904/1126005_1.JPG',
        'https://pds.medicaltimes.com/NewsPhoto/202005/1133768_1.jpg',
      ],
    };
  },
  created() {
    // 페이지 생성시 sido정보를 가져온다
    http.get('/map/getSido').then(({ data }) => {
      this.sidoOptions = data;
    });
  },
  methods: {
    // 시도를 선택하면 그에 맞는 구군을 보여주자 ㅎ
    onChangeSido() {
      http
        .get('/map/getGugun', {
          params: { sido_code: this.sidoSelected.sido_code },
        })
        .then(({ data }) => {
          this.gugunOptions = data;
        });
    },
    // 버튼이 눌리면 검색을 하자!
    searchCovid() {
      this.result = [];
      http
        .get('/covid/getCovid', {
          params: {
            city: this.sidoSelected.sido_name,
            gugun: this.gugunSelected.gugun_name,
          },
        })
        .then((res) => {
          for (let i = 0; i < res.data.length; i++) {
            this.result.push({
              city: res.data[i].city,
              gugun: res.data[i].gugun,
              hospital_name: res.data[i].hospital_name,
              phone: res.data[i].phone,
              address: res.data[i].address,
            });
            this.getPosition(res.data[i]);
          }
        })
        .catch((err) => console.log(err));
    },
    searchHospital() {
      this.result = [];
      http
        .get('/covid/getHospital', {
          params: {
            city: this.sidoSelected.sido_name,
            gugun: this.gugunSelected.gugun_name,
          },
        })
        .then(({ data }) => {
          console.log(data);
          this.result.push(data);
        })
        .catch((err) => console.log(err));
    },
    getPosition(a) {
      this.markerInfo = [];
      console.log('제발 시작해>>>');
      var gMapApiKey = 'AIzaSyCfChyPiLuJQ6W1PdawiqgHx8F2NXX5snE';
      var address = a.address; // 주소를 가지고
      let tmpLat;
      let tmpLng;
      axios
        .get(
          `https://maps.googleapis.com/maps/api/geocode/json?address=${address}&key=${gMapApiKey}` // api요청을 통해
        )
        .then((response) => {
          // 위치값을 받아온다.
          console.log('응답쓰');
          console.log(response);
          tmpLat = response.data.results[0].geometry.location.lat;
          tmpLng = response.data.results[0].geometry.location.lng;
          // markerInfo에 넣어준다
          this.markerInfo.push({
            position: {
              lat: tmpLat,
              lng: tmpLng,
            },
            phone: a.phone, // 구분 코드 : A0, 97, 99
            city: a.city, // 전화번호
            gugun: a.gugun, // 기관명
          });
        })
        .catch((err) => console.log(err));
      console.log(this.markerInfo);
    },
    // ////////////// 마커 클릭 이벤트를 처리하기 위한 메소드들 ////////////////
    // clickMarker(m) {
    //   this.center.lat = m.position.lat;
    //   this.center.lng = m.position.lng;
    //   this.center.zoom = 17;
    // },
    // ////////// 마커 클릭시 창(말풍선)을 띄우기 위한 메소드 ////////////
    // openWindow(m) {
    //   this.markerWindowInfo.window_open = true;
    //   this.markerWindowInfo.infowindow.lat = m.position.lat; // 여기도 이렇게 안줘도 되네,, 일단 나중으로,,
    //   this.markerWindowInfo.infowindow.lng = m.position.lng;
    //   this.markerWindowInfo.info_marker = m;
    // },
    // resultCardClick(latitude, longitude) {
    //   this.center.lat = latitude;
    //   this.center.lng = longitude;
    //   this.center.zoom = 18;
    // },
  },
};
</script>

<style></style>
