<template>
  <div>
    <br />

    <b-card
      id="usercard"
      overlay
      img-src="https://images.unsplash.com/photo-1513689125086-6c432170e843?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1267&q=80"
      img-alt="Card Image"
      title="찜한 목록"
    >
      <b-card-text> </b-card-text>
    </b-card>
    <b-row>
      <div style="margin-left:30px; margin-right:30px">
        <div
          v-for="f in favoriteList"
          :key="f.idx"
          style="float:left; margin-left:15px; margin-top:20px"
        >
          <b-card
            :img-src="srcLink[parseInt(Math.random() * 7)]"
            img-alt="Image"
            img-top
            tag="article"
            style="max-width: 20rem;"
            class="mb-2"
            img-height="215px"
          >
            <template #header>
              <h4 class="mb-0">{{ f.name }}</h4>
            </template>
            <!-- 2. 그 정보를 뿌려준다. -->
            <b-card-text>
              <table>
                <!-- 3. idx가 잘 오고있네? -->
                <!-- <tr>
              <th>확인을 해보자</th>
              <td>{{ f.idx }}</td>
            </tr> -->

                <tr>
                  <th>건축일자</th>
                  <td>{{ f.buildYear }}년</td>
                </tr>
                <tr>
                  <th>면적</th>
                  <td>{{ f.area }}㎡</td>
                </tr>
                <tr>
                  <th>주소</th>
                  <td>{{ f.address }}</td>
                </tr>
              </table>
            </b-card-text>
            <!-- 4. 이 삭제버튼을 누르면, del이라는 함수와 함께 너가 누른 favorite 카드의 번호(idx)를 줘야겠지?! -->
            <!-- 그때 여기에 주면 될거야... 아직 확신이 없다...ㅎㅋㅋ한번넝ㅋㅋ -->
            <b-button
              variant="danger"
              active
              @click="del(f.idx)"
              style="color:white"
              >삭제</b-button
            >
          </b-card>
        </div>
      </div>
    </b-row>
    <!-- 있는 정보 없는 정보 있음 v-if 처리해서 있는 정보만 뿌려줘야함 -->
  </div>
</template>

<script>
import axios from 'axios';
import http from '@/util/http-common';

export default {
  created() {
    var userid = localStorage.getItem('user-id');
    http
      .get(`/favorite/getFavorites/${userid}`)
      .then(({ data }) => {
        console.log(data);
        this.favoriteList = data; // 1. 서버가 준 정보를 담아서 저장한다.
      })
      .catch((err) => {
        console.log(err);
      });
  },
  data() {
    return {
      favoriteList: [],
      srcLink: [
        'https://images.unsplash.com/photo-1563657884956-1603efcca817?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80',
        'https://images.unsplash.com/photo-1512917774080-9991f1c4c750?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80',
        'https://images.unsplash.com/photo-1602517263263-730e18a201ba?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=967&q=80',
        'https://images.unsplash.com/photo-1597335590872-782da3d5f1c1?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2024&q=80',
        'https://images.unsplash.com/photo-1576941089067-2de3c901e126?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1143&q=80',
        'https://images.unsplash.com/photo-1591474200742-8e512e6f98f8?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=967&q=80',
        'https://images.unsplash.com/photo-1584738766473-61c083514bf4?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80',
      ],
    };
  },
  methods: {
    del(data) {
      console.log(data);
      // a태그때문에 경로 이동해버려서 그런거같아유 넹 ㅎㅎ 그럼 저 data를 던져주자
      axios
        .get(`http://localhost:8080/happyhouse/favorite/delete/${data}`)
        .then((res) => {
          console.log(res.data);
          var userid = localStorage.getItem('user-id');
          http
            .get(`/favorite/getFavorites/${userid}`)
            .then(({ data }) => {
              console.log(data);
              this.favoriteList = data; // 1. 서버가 준 정보를 담아서 저장한다.
            })
            .catch((err) => {
              console.log(err);
            });
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style></style>
