<template>
  <div>
    <br />
    <h3>공지사항 등록</h3>
    <!--등록은 관리자만-->
    <div id="make">
      <b-container fluid>
        <b-form @submit.prevent="send">
          <b-row class="my-1">
            <b-col sm="2">
              <label for="input-default">제목: </label>
            </b-col>
            <b-col sm="10">
              <b-form-input
                id="input-default"
                name="title"
                placeholder="제목을 입력하세요"
                v-model="n.title"
                required
              ></b-form-input>
            </b-col>
          </b-row>

          <b-row class="my-1">
            <b-col sm="2">
              <label for="input-default">내용:</label>
            </b-col>
            <b-col sm="10">
              <b-form-textarea
                id="ta"
                name="content"
                v-model="n.content"
                placeholder="내용을 입력하세요"
                debounce="500"
                rows="10"
                max-rows="100"
                required
              ></b-form-textarea>
              <pre class="mt-2 mb-0"></pre>
            </b-col>
          </b-row>
          <b-button type="submit" variant="dark" style="color:white;">
            등록
          </b-button>
        </b-form>
      </b-container>
    </div>
    <br />
  </div>
</template>

<script>
import axios from 'axios';

export default {
  created() {
    console.log(this.$session.getAll());
  },
  data() {
    return {
      n: {
        title: '',
        content: '',
      },
      editor: '',
    };
  },
  methods: {
    send() {
      axios
        .post('http://localhost:8080/happyhouse/notice/insert', this.n)
        .then((res) => {
          console.log(res.data);
          this.$router.push('/happyhouse/notice/list');
        })
        .catch((ex) => console.log(ex));
    },
    pasteCapture(evt) {
      let text, onPasteStripFormattingIEPaste;
      evt.preventDefault();
      if (evt.originalEvent && evt.originalEvent.clipboardData.getData) {
        text = evt.originalEvent.clipboardData.getData('text/plain');
        this.$refs.editor_ref.runCmd('insertText', text);
      } else if (evt.clipboardData && evt.clipboardData.getData) {
        text = evt.clipboardData.getData('text/plain');
        this.$refs.editor_ref.runCmd('insertText', text);
      } else if (window.clipboardData && window.clipboardData.getData) {
        if (!onPasteStripFormattingIEPaste) {
          onPasteStripFormattingIEPaste = true;
          this.$refs.editor_ref.runCmd('ms-pasteTextOnly', text);
        }
        onPasteStripFormattingIEPaste = false;
      }
    },
  },
};
</script>

<style>
a:link {
  text-decoration: none;
  color: rgb(43, 76, 124);
}
a:visited {
  text-decoration: none;
  color: rgb(43, 76, 124);
}
a:active {
  text-decoration: none;
  color: rgb(43, 76, 124);
}
a:hover {
  text-decoration: none;
  color: rgb(164, 180, 211);
}
</style>
