                        v-model="user.userpwd"
                        required
                        placeholder="비밀번호 입력"
                        @keypress.enter="login"
                      ></b-form-input>
                    </b-form-group>
                    <b-button
                      variant="secondary"
                      @click="hide()"
                      style=" margin-left:300px"
                      ><router-link to="/happyhouse/pchange" style="color:white"
                        >비밀번호 변경</router-link
                      ></b-button
                    >
                  </b-form>
                  <!-- 😎😎😎😎😎😎😎 카카오  -->
                  <!-- <b-button :href="kakao_url">카카오 계정으로 로그인</b-button> -->
                </template>
                <template v-slot:modal-footer="{ cancel }">
                  <b-form>
                    <b-button
                      type="button"
                      variant="primary"
                      class="m-1"
                      @click="login"
                    >
                      <b-icon icon="person-check-fill" font-scale="1"></b-icon
                      >&nbsp;로그인
                    </b-button>
                    <b-button
                      variant="danger"
                      class="float-right"
