                    >
                      취소
                    </b-button>
                  </b-form>
                </template>
              </b-modal>
              <b-dropdown-divider></b-dropdown-divider>
              <!-- 회원가입 조건 걸고싶은거 맘대루 걸어주세염 😊💕 -->
              <b-dropdown-item v-b-modal.modal-2>회원가입</b-dropdown-item>
              <!--ref="join-modal" 모달닫히게 하는 속성-->
              <b-modal
                ref="join-modal"
                id="modal-2"
                title="회원가입"
                :class="{ in: modalShown }"
              >
                <b-form @submit.stop.prevent>
                  <!-- 아이디 -->
                  <label for="feedback-user">아이디 : </label>
                  <b-form-input
                    v-model="join.userid"
                    :state="validationId"
                    id="feedback-user-id"
                  ></b-form-input>
                  <b-form-invalid-feedback :state="validationId">
                    Your user ID must be 5-12 characters long.
                  </b-form-invalid-feedback>
                  <b-form-valid-feedback :state="validationId">
                    Looks Good.
                  </b-form-valid-feedback>
                  <!-- idCheck가 true가 되면 variant의 값을 바꿔주게끔 해야함 -> 체크 완료되면 초록색됩니다 -->
                  <b-button
                    :variant="this.idCheck ? 'success' : 'danger'"
                    @click="idCheckFunc"
                    >아이디 중복체크</b-button
                  >
                  <br /><br />
                  <!-- 비밀번호 -->
                  <label for="feedback-user">비밀번호 : </label>
                  <b-form-input
                    type="password"
                    v-model="join.userpwd"
                    :state="validationPwd"
                    id="feedback-user-pwd"
                  ></b-form-input>
                  <b-form-invalid-feedback :state="validationPwd">
                    Your password must be 8-20 characters long, contain letters
                    and numbers, and must not contain spaces, special
                    characters, or emoji.
                  </b-form-invalid-feedback>
                  <b-form-valid-feedback :state="validationPwd">
                    Looks Good.
                  </b-form-valid-feedback>
                  <!-- 이름 -->
                  <label for="feedback-user">이름 : </label>
                  <b-form-input
                    type="text"
                    v-model="join.username"
                  ></b-form-input>
                  <!-- 이메일 -->
                  <label for="feedback-user">이메일 : </label>
                  <b-form-input
                    type="email"
                    v-model="join.email"
                  ></b-form-input>
                  <!-- 번호 -->
                  <label for="feedback-user">번호 : </label>
                  <!-- default 국가를 한국으로 해주세염 😊 -->
                  <VuePhoneNumberInput
                    v-model="join.phone"
                    style="width:465px"
                  />
                </b-form>
                <template v-slot:modal-footer="{ cancel }">
                  <b-form>
                    <b-button
                      type="button"
                      variant="primary"
