            </b-nav-item-dropdown>
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>

    <!--body-->
    <router-view />
    <br />
    <br />
    <!--footer-->
    <div class="footer">
      <br />
      <b-icon class="ic" icon="camera" font-scale="2" style="white"></b-icon
      >&nbsp;&nbsp;
