      <b-icon
        class="ic"
        icon="cloud-arrow-down"
        font-scale="2"
        style="white"
      ></b-icon>
      <b-icon class="ic" icon="share" font-scale="2" style="white"></b-icon>
      <br />
      <p>
        <a href="#" class="a1"> 회사소개 </a> |
        <a href="#" class="a1"> 채용정보 </a> |
        <a href="#" class="a1"> 이용약관 </a> |
        <a href="#" class="a1"> 개인정보 처리방침 </a> |
        <a href="#" class="a1"> 서비스 이용약관 </a>
      </p>
      <p>
        상호 : (주)happyhouse | 대표 : 김다윤 , 유진이 | 사업자 등록번호 :
        06422545-40550553
      </p>
      <p>
        주소 : 대전광역시 유성구 덕명동 124 207호(3반)
      </p>
      <p>문의 : 042-123-1234 | email : ssafy@happyhouse.com</p>
      <br />
    </div>
