</template>

<script>
import http from '@/util/http-common';
import { mapGetters } from 'vuex';
import VuePhoneNumberInput from 'vue-phone-number-input';
import 'vue-phone-number-input/dist/vue-phone-number-input.css';
import SideToggleBar from '@/views/SideToggleBar.vue';

export default {
  data() {
    return {
      isLogin: false,
      idCheck: false,
      user: {
        userid: '',
        userpwd: '',
      },
      msg: '',
      join: {
        userid: '',
        userpwd: '',
        username: '',
        email: '',
        phone: '',
      },
      isAdmin: false,
      modalShown: false,
    };
  },
  components: {
    VuePhoneNumberInput,
    SideToggleBar,
  },
  computed: {
    ...mapGetters([
      'getAccessToken',
      'getUserid',
      'getUsername',
      'getIsAuthenticated',
      'getIsAdmin',
    ]),
    nextRoute() {
      return this.$route.params.nextRoute ? this.$route.params.nextRoute : '';
    },
    validationId() {
      return this.join.userid.length > 4 && this.join.userid.length < 13;
    },
    validationPwd() {
      var pwd = this.join.userpwd;

      var num = pwd.search(/[0-9]/g);
      var eng = pwd.search(/[a-z]/gi);
      var spe = pwd.search(/[`~!@@#$%^&*|₩₩₩'₩";:₩/?]/gi);

      return (
        pwd != '' &&
        pwd.length > 7 &&
        pwd.length < 21 &&
        ((num < 0 && eng < 0) ||
          (eng < 0 && spe < 0) ||
          (spe < 0 && num < 0)) &&
        pwd.search(/\s/) == -1
      );
    },
  },
  methods: {
    onClickLogout() {
      this.$store
        .dispatch('LOGOUT')
        .then(() => this.$router.replace('/happyhouse').catch(() => {}));
    },
    login() {
      // 로그인 버튼 누르면 실행 되는곳이니까
      // 요청이 성공했든 실패했든 입력한 아이디랑 비밀번호를 날리려면
      // axios요청하고 제일 마지막에 비우는게 좋으니까
      // 이럴땐 .finally() 안쪽에서 수행
      this.$store
        .dispatch('LOGIN', this.user)
        .then(() => {
          this.adminCheck(this.user.userid);
          this.$router.replace(`/happyhouse/${this.nextRoute}`);
        })
        .catch(({ message }) => (this.msg = message))
        .finally(() => {
          this.user.userid = '';
          this.user.userpwd = ''; // 다시로그인할때 전에 로그인 기록 지워주기
        });
    },
    adminCheck(userid) {
      http
        .post('/user/adminCheck', userid)
        .then(({ data }) => {
          if (data == 'isAdmin') {
            this.isAdmin = true;
            this.$store
              .dispatch('ISADMIN', userid)
              .then()
              .catch(({ message }) => (this.msg = message));
          } else {
            this.isAdmin = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    checkAdmin() {
      if (localStorage.getItem('admin') == true) {
        this.isAdmin = true;
      } else {
        this.isAdmin = false;
      }
    },
    idCheckFunc() {
      // this.join.userid가 데이터베이스에 있는지 확인하기
      http
        .get(`/user/idCheck/${this.join.userid}`)
        .then(({ data }) => {
          if (data == 'exist') {
            // db에 아이디가 있으면
            alert('중복되는 아이디입니다. 다른 아이디를 입력해주세요!');
          } else if (data == 'non-exist') {
            // db에 아이디가 없으면
            alert('사용가능한 아이디입니다.');
            this.idCheck = true;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    joinValidationCheck() {
      // idCheck가 false이면 idCheck가 필요합니다!하고 실행안되게
      if (!this.idCheck) {
        alert('아이디 중복체크를 해주세요!');
        return;
      }
    },
    joinStart() {
      http
        .post('/user/join', this.join)
        .then(({ data }) => {
          if (data == 'join-success') {
            alert('회원가입 성공!');
            // 아까 ref설정한거 이용해서
            // 자바스크립트쪽에서 HTML 요소 접근 후에
            // Modal에 있는 hide함수 이용해서 모달 닫기wow...
            this.$refs['join-modal'].hide();
          } else {
            alert('회원가입에 실패했습니다!');
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    ///////////// 카카오 로그인 😎😎😎😎😎😎😎😎 /////////////////
    http
      .get('/memberloginform.do')
      .then((response) => {
        this.kakao_url = response.data.kakao_url;
      })
      .catch((err) => console.log(err));

    if (localStorage.getItem('auth-token')) {
      this.isLogin = true;
      this.$store.commit('setAuth', localStorage);
      this.isAdmin = this.getIsAdmin; // 관리자일때 새로고침하면 로그인 안풀리는데 메뉴바 바뀌는거 처리
    }
  },
};
</script>

