.footer {
  /* position: fixed; */
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #2c3e50;
  color: white;
  text-align: center;
}
.a1 {
  color: white !important;
  text-decoration: none;
  margin-left: 15px;
}
.ic {
  margin-left: 15px;
}
</style>
