import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

const SERVER_URL = "http://localhost:8080/happyhouse";

export default new Vuex.Store({
  state: {
    accessToken: null,
    userid: "",
    username: "",
    isAuthenticated: false,
    isAdmin : false,
  },
  getters: {
    getAccessToken(state) {
      return state.accessToken;
    },
    getUserid(state) {
      return state.userid;
    },
    getUsername(state) {
      return state.username;
    },
    getIsAuthenticated(state) {
        return state.isAuthenticated;
    },
    getIsAdmin(state){
      return state.isAdmin;
    }
  },
  mutations: {
    LOGIN(state, payload) {
      state.accessToken = payload["auth-token"];
      state.userid = payload["user-id"];
      state.username = payload["user-name"];
      state.isAuthenticated = true;
      localStorage["auth-token"] = payload["auth-token"];
      localStorage["user-id"] = payload["user-id"];
      localStorage["user-name"] = payload["user-name"];
    },
    LOGOUT(state) {
      state.accessToken = null;
      state.userid = "";
      state.username = "";
      state.isAuthenticated = false;
      localStorage["auth-token"] = "";
      localStorage["user-id"] = "";
      localStorage["user-name"] = "";
      localStorage["admin"] = "";
    },
    setAuth(state, payload) {
      state.accessToken = payload["auth-token"];
      state.userid = payload["user-id"];
      state.username = payload["user-name"];
      state.isAuthenticated = true;

      // TODO : 새로고침시 관리자 확인 임시방편
      state.isAdmin = payload["admin"];
    },
    ISADMIN(state){
      state.isAdmin = true;
      localStorage["admin"] = true;
    }
  },
  actions: {
    LOGIN(context, user) {
      return axios
        .post(`${SERVER_URL}/user/confirm/login`, user)
        .then((response) => {
          context.commit("LOGIN", response.data);
          axios.defaults.headers.common[
            "auth-token"
          ] = `${response.data["auth-token"]}`;
        });
    },
    LOGOUT(context) {
      context.commit("LOGOUT");
      axios.defaults.headers.common["auth-token"] = undefined;
    },
    ISADMIN(context){
      context.commit("ISADMIN");
    }
  },
  modules: {}
});