<template>
  <div>
    <br />
    <h3>내정보 수정</h3>
    <br />
    <div id="mypage">
      <b-container fluid>
        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default">ID:</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              style="margin-left:10px"
              readonly
              v-bind:value="userInfo.userid"
            ></b-form-input>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default">Password:</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              style="margin-left:10px"
              v-model="userInfo.userpwd"
            ></b-form-input>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default" style="width:105px">Name :</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              style="margin-left:10px"
              v-model="userInfo.username"
            ></b-form-input>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default" style="width:105px">Email :</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              style="margin-left:10px"
              v-model="userInfo.email"
            ></b-form-input>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default" style="width:110px">Phone-Number:</label>
          </b-col>
          <b-col sm="10">
            <VuePhoneNumberInput
              v-model="userInfo.phone"
              style="margin-left:10px; width:500px"
            />
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default" style="width:110px">Join-Date:</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              style="margin-left:10px"
              readonly
              v-bind:value="userInfo.joindate"
            ></b-form-input>
          </b-col>
        </b-row>
      </b-container>
    </div>
    <br />
    <b-button variant="dark" @click="update">수정</b-button>
    <!--<b-button variant="danger" id="del"><a href="#" @click="del"  style="color:white">탈퇴하기</a></b-button>-->
  </div>
</template>

<script>
import http from '@/util/http-common';
import VuePhoneNumberInput from 'vue-phone-number-input';
import 'vue-phone-number-input/dist/vue-phone-number-input.css';

export default {
  data() {
    return {
      userInfo: {},
    };
  },
  components: {
    VuePhoneNumberInput,
  },
  created() {
    let id = localStorage.getItem('user-id');
    http
      .get(`/user/userInfo/${id}`)
      .then(({ data }) => {
        this.userInfo = data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    update() {
      http
        .post('/user/update', this.userInfo)
        .then(({ data }) => {
          if (data == 'update-success') {
            alert('수정이 완료되었습니다.');
            this.$router.push('/happyhouse/mypage/check');
          } else {
            alert('비밀번호를 정확히 입력해주세요');
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style>
#mypage {
  display: inline-block;
}
#del {
  margin-left: 15px;
  background-color: rgb(236, 127, 127);
  border: rgb(236, 127, 127);
  width: 250px;
}
#phone {
  width: 150px;
  float: left;
  margin-left: 10px;
}
</style>
