<template>
  <div class="here">
    <br />
    <b-card
      id="noticecard"
      overlay
      img-src="https://images.unsplash.com/photo-1580656449194-30bb3dfb0b76?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1055&q=80"
      img-alt="Card Image"
      title="NOTICE"
      sub-title="공지사항 페이지 입니다."
    >
      <b-card-text> </b-card-text>
    </b-card>
    <b-input-group class="mb-2" id="search">
      <b-input-group-prepend is-text>
        <b-icon icon="search"></b-icon>
      </b-input-group-prepend>
      <b-form-input
        type="text"
        v-model="keyword"
        placeholder="검색어를 입력하세요"
      ></b-form-input>
      <b-button id="sbtn" @click="search">검색</b-button>
    </b-input-group>

    <b-table-simple responsive>
      <b-tr>
        <b-th>번호</b-th>
        <b-th>제목</b-th>
        <b-th>작성자</b-th>
        <b-th>날짜</b-th>
      </b-tr>
      <b-tr v-for="n in notices" :key="n.num">
        <b-td>{{ n.num }}</b-td>
        <b-td>
          <router-link :to="'/happyhouse/notice/read/' + n.num">{{
            n.title
          }}</router-link>
        </b-td>
        <!-- read 할 때? -->
        <b-td>{{ n.writer }}</b-td>
        <b-td>{{ n.writeDate }}</b-td>
      </b-tr>
    </b-table-simple>
    <b-button variant="dark" id="all"
      ><a href="#" @click="list" style="color:white">목록 모두보기</a></b-button
    >
    <div v-if="isAdmin">
      <b-button variant="warning">
        <router-link to="/happyhouse/notice/add" style="color:white"
          >작성하기</router-link
        ></b-button
      >
    </div>
    <!-- 관리자일때만 버튼 보이게 -->
  </div>
</template>

<script>
import axios from 'axios';

export default {
  created() {
    axios
      .get('http://localhost:8080/happyhouse/notice/list')
      .then((res) => {
        // console.log(res);
        this.notices = res.data;
      })
      .catch((err) => console.log(err));
    var u = localStorage.getItem('admin');
    if (u == 'true') isAdmin = true;
  },
  data() {
    return {
      keyword: '',
      notices: [],
      isAdmin: false,
    };
  },
  methods: {
    search() {
      axios
        .get(`http://localhost:8080/happyhouse/notice/search/${this.keyword}`)
        .then((res) => {
          console.log(res.data);
          this.notices = res.data;
        })
        .catch((err) => console.log(err));
    },
    list() {
      axios
        .get('http://localhost:8080/happyhouse/notice/list')
        .then((res) => {
          // console.log(res);
          this.notices = res.data;
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>

<style>
#sbtn {
  margin-left: 20px;
  margin-right: 50px;
}
#search {
  width: 450px;
  float: right;
}
#noticecard img {
  height: 300px;
}
#noticecard h4 {
  font-size: 70px;
  font-weight: bold;
  color: rgb(4, 34, 46);
}
#noticecard h6 {
  color: rgb(4, 34, 46) !important;
  display: inline-block;
}
#all {
  margin-right: 15px;
}
</style>
