<template>
  <div>
    <br />
    <h3>FAQ 등록</h3>
    <div id="make">
      <b-container fluid>
        <b-form @submit.prevent="send">
          <b-row class="my-1">
            <b-col sm="2">
              <label for="input-default">제목: </label>
            </b-col>
            <b-col sm="10">
              <b-form-input
                id="input-default"
                name="qtitle"
                placeholder="제목을 입력하세요"
                v-model="qna.qtitle"
                required
              ></b-form-input>
            </b-col>
          </b-row>

          <b-row class="my-1">
            <b-col sm="2">
              <label for="input-default">내용:</label>
            </b-col>
            <b-col sm="10">
              <b-form-textarea
                id="ta"
                name="qcontent"
                v-model="qna.qcontent"
                placeholder="내용을 입력하세요"
                debounce="500"
                rows="10"
                max-rows="100"
                required
              ></b-form-textarea>
              <pre class="mt-2 mb-0"></pre>
            </b-col>
          </b-row>
          <br />

          <b-button type="submit" variant="dark" style="color:white;">
            등록
          </b-button>
        </b-form>
      </b-container>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  created() {
    console.log(this.$session.getAll());
  },
  data() {
    return {
      qna: {
        qtitle: '',
        qcontent: '',
      },
    };
  },
  methods: {
    send() {
      axios
        .post('http://localhost:8080/happyhouse/qna/insert', this.qna)
        .then((res) => {
          console.log(res.data);
          this.$router.push('/happyhouse/qna/list');
        })
        .catch((ex) => console.log(ex));
    },
  },
};
</script>

