import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/Home.vue";
import News from "@/views/News.vue";
import Favorite from "@/views/Favorite.vue";
import LookAround from "@/views/LookAround.vue";
import Corona from "@/components/lookaround/Corona.vue";
// 매매 전월세 
import AptDealRent from "@/views/DealRent/AptDealRent.vue";
import RowHouseDealRent from "@/views/DealRent/RowHouseDealRent.vue";
import SingleMultiDealRent from "@/views/DealRent/SingleMultiDealRent.vue";
import OfficetelDealRent from "@/views/DealRent/OfficetelDealRent.vue";
// 마이페이지
import Mypage from "@/views/Mypage.vue";
import MypageCheck from "@/components/mypage/MypageCheck.vue";
import MypageModify from "@/components/mypage/MypageModify.vue";
// 로그인 전 비밀번호 변경
import PwChange from "../views/PwChange.vue";
// Q&A
import Qna from "@/views/Qna.vue";
import QnaList from "@/components/qna/QnaList.vue";
import QnaAdd from "@/components/qna/QnaAdd.vue";
import QnaRead from "@/components/qna/QnaRead.vue";
import QnaModify from "@/components/qna/QnaModify.vue";
//notice
import Notice from "@/views/Notice.vue";
import NoticeList from "@/components/notice/NoticeList.vue";
import NoticeAdd from "@/components/notice/NoticeAdd.vue";
import NoticeRead from "@/components/notice/NoticeRead.vue";
import NoticeModify from "@/components/notice/NoticeModify.vue";
//manage
import Manage from "@/views/Manage.vue";
import UserList from "@/components/usermanage/UserList.vue";
import UserAdd from "@/components/usermanage/UserAdd.vue";
import UserRead from "@/components/usermanage/UserRead.vue";
import UserModify from "@/components/usermanage/UserModify.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/happyhouse",
    name: "Home",
    component: Home
  },
  {
    path: "/happyhouse/apt",
    name: "AptDealRent",
    component: AptDealRent
  },
  {
    path: "/happyhouse/rowHouse",
    name: "RowHouseDealRent",
    component: RowHouseDealRent
  },
  {
    path: "/happyhouse/singleMulti",
    name: "SingleMultiDealRent",
    component: SingleMultiDealRent
  },
  {
    path: "/happyhouse/officetel",
    name: "OfficetelDealRent",
    component: OfficetelDealRent
  },
  {
    path: "/happyhouse/news",
    name: "News",
    component: News
  },
  {
    path: "/happyhouse/favorite",
    name: "Favorite",
    component: Favorite
  },
  {
    path: '/happyhouse/pchange',
    name: "PwChange",
    component : PwChange
  },
  {
    path: "/happyhouse/look",
    name: "LookAround",
    component: LookAround,
    redirect: "/happyhouse/look/corona",
      children: [
        {
          path: "corona",
          name: "Corona",
          component: Corona
        },
        // {
        //   path: "medical",
        //   name: "Medical",
        //   component: Medical
        // },
      ] 
  },
  {
    path: "/happyhouse/mypage",
    name: "Mypage",
    component: Mypage,
    redirect: "/happyhouse/mypage/check",
    children: [
      {
        path: "check",
        name: "MypageCheck",
        component: MypageCheck
      },
      {
        path: "modify",
        name: "MypageModify",
        component: MypageModify
      }
    ]
  },
  {
    path: "/happyhouse/qna",
    name: "Qna",
    component: Qna,
    redirect: "/happyhouse/qna/list",
    children: [
      {
        path: "list",
        name: "QnaList",
        component: QnaList
      },
      {
        path: "add",
        name: "QnaAdd",
        component: QnaAdd
      },
      {
        path: "read/:qnum",
        name: "QnaRead",
        component: QnaRead
      },
      {
        path: "modify/:qnum",
        name: "QnaModify",
        component: QnaModify
      }
    ]
  },
  {
    path: "/happyhouse/notice",
    name: "Notice",
    component: Notice,
    redirect: "/happyhouse/notice/list",
    children: [
      {
        path: "list",
        name: "NoticeList",
        component: NoticeList,
      },
      {
        path: "add",
        name: "NoticeAdd",
        component: NoticeAdd
      },
      {
        path: "read/:num",
        name: "NoticeRead",
        component: NoticeRead
      },
      {
        path: "modify/:num",
        name: "NoticeModify",
        component: NoticeModify
      }
    ]
  },
  {
    path: "/happyhouse/manage",
    name: "Manage",
    component: Manage,
    redirect: "/happyhouse/manage/list",
    children: [
      {
        path: "list",
        name: "UserList",
        component: UserList
      },
      {
        path: "add",
        name: "UserAdd",
        component: UserAdd
      },
      {
        path: "read/:userid",
        name: "UserRead",
        component: UserRead
      },
      {
        path: "modify/:userid",
        name: "UserModify",
        component: UserModify
      }
    ]
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
