<template>
  <div>
    <br />
    <h3>FAQ 상세보기</h3>
    <div id="t">
      <b-table-simple responsive>
        <b-tr>
          <b-th id="h">글번호</b-th>
          <b-td>{{ qna.qnum }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">글제목</b-th>
          <b-td>{{ qna.qtitle }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">작성자</b-th>
          <b-td>{{ qna.qwriter }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">작성일</b-th>
          <b-td>{{ qna.qwritedate }}</b-td>
        </b-tr>
        <b-tr>
          <b-th id="h">내용</b-th>
          <b-td style="white-space: pre-line;">{{ qna.qcontent }}</b-td>
        </b-tr>
      </b-table-simple>
      <div v-if="isAdmin">
        <b-button variant="warning"
          ><a href="#" @click="modify" style="color:white"> 수정 </a></b-button
        >
        <b-button variant="dark" id="d"
          ><a href="#" @click="del" style="color:white"> 삭제 </a></b-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      qnum: 0,
      qna: {},
      isAdmin: false,
    };
  },
  created() {
    this.qnum = this.$route.params.qnum;
    console.log(this.qnum);
    axios
      .get(`http://localhost:8080/happyhouse/qna/detail/${this.qnum}`) // http://localhost:8080/happyhouse/qna/detail/읽으려는번호
      .then((res) => {
        console.log(res.data);
        this.qna = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
    var u = localStorage.getItem('admin');
    if (u == 'true') this.isAdmin = true;
  },
  methods: {
    modify() {
      this.$router.push('/happyhouse/qna/modify/' + this.qnum);
    },
    del() {
      this.qnum = this.$route.params.qnum;
      axios
        .get(`http://localhost:8080/happyhouse/qna/delete/${this.qnum}`) // http://localhost:8080/happyhouse/qna/detail/읽으려는번호
        .then((res) => {
          console.log(res.data);
          this.$router.push('/happyhouse/qna/list');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style>
#d {
  margin-left: 15px;
}
#h {
  background-color: rgb(169, 218, 197);
  width: 160px;
}
#t {
  width: 900px;
  display: inline-block;
}
</style>
