<template>
  <div>
    <br />
    <h3>내 정보보기</h3>
    <br />
    <div id="mtpage">
      <b-container fluid>
        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default">ID:</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              readonly
              v-bind:value="userInfo.userid"
            ></b-form-input>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default" style="width:100px">Name :</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              readonly
              v-bind:value="userInfo.username"
            ></b-form-input>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default">Email:</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              readonly
              v-bind:value="userInfo.email"
            ></b-form-input>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default">Phone-Number:</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              readonly
              v-bind:value="userInfo.phone"
            ></b-form-input>
          </b-col>
        </b-row>

        <b-row class="my-1">
          <b-col sm="2">
            <label for="input-default">Join-Date:</label>
          </b-col>
          <b-col sm="10">
            <b-form-input
              id="input-default"
              readonly
              v-bind:value="userInfo.joindate"
            ></b-form-input>
          </b-col>
        </b-row>
      </b-container>
      <br />
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';

export default {
  data() {
    return {
      userInfo: {},
    };
  },
  created() {
    let id = localStorage.getItem('user-id');
    http
      .get(`/user/userInfo/${id}`)
      .then(({ data }) => {
        this.userInfo = data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>
<style>
#mtpage {
  display: inline-block;
}
</style>
