<template>
  <div>
    <br />
    <h3>회원 등록</h3>
    <div id="make">
      <b-table-simple responsive>
        <b-tr>
          <b-th>아이디</b-th>
          <b-td>
            <b-form-input
              type="text"
              name="userid"
              v-model="u.userid"
              required
            />
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>비밀번호</b-th>
          <b-td>
            <b-form-input
              type="password"
              name="userpwd"
              v-model="u.userpwd"
              required
            />
          </b-td>
        </b-tr>
        <tr>
          <b-th>이름</b-th>
          <b-td>
            <b-form-input
              type="text"
              name="username"
              v-model="u.username"
              required
            />
          </b-td>
        </tr>

        <b-tr>
          <b-th>이메일</b-th>
          <b-td>
            <b-form-input
              type="email"
              name="email"
              v-model="u.email"
              required
            />
          </b-td>
        </b-tr>
        <b-tr>
          <b-th>핸드폰번호</b-th>
          <b-td><VuePhoneNumberInput v-model="u.phone"/></b-td>
        </b-tr>
      </b-table-simple>

      <b-button variant="dark"
        ><a href="#" @click="send" style="color:white">등록</a></b-button
      >
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import VuePhoneNumberInput from 'vue-phone-number-input';
import 'vue-phone-number-input/dist/vue-phone-number-input.css';
export default {
  created() {
    console.log(this.$session.getAll());
  },
  data() {
    return {
      u: {
        userid: '',
        userpwd: '',
        username: '',
        email: '',
        phone: '',
        joindate: '',
      },
    };
  },
  components: {
    VuePhoneNumberInput,
  },
  methods: {
    send() {
      axios
        .post('http://localhost:8080/happyhouse/user/insert', this.u)
        .then((res) => {
          console.log(res.data);
          this.$router.push('/happyhouse/manage/list');
        })
        .catch((ex) => console.log(ex));
    },
  },
};
</script>

<style>
a:link {
  text-decoration: none;
  color: rgb(43, 76, 124);
}
a:visited {
  text-decoration: none;
  color: rgb(43, 76, 124);
}
a:active {
  text-decoration: none;
  color: rgb(43, 76, 124);
}
a:hover {
  text-decoration: none;
  color: rgb(164, 180, 211);
}
</style>
