<template>
  <div>
    <br />
    <h3>공지사항 수정</h3>
    <!-- 공지사항 수정은 관리자만 -->
    <div id="make">
      <b-container fluid>
        <b-form @submit.prevent="update">
          <b-row class="my-1">
            <b-col sm="2">
              <label for="input-default">제목: </label>
            </b-col>
            <b-col sm="10">
              <b-form-input
                id="input-default"
                v-model="n.title"
                required
              ></b-form-input>
            </b-col>
          </b-row>

          <b-row class="my-1">
            <b-col sm="2">
              <label for="input-default">내용:</label>
            </b-col>
            <b-col sm="10">
              <b-form-textarea
                id="ta"
                v-model="n.content"
                debounce="500"
                rows="10"
                max-rows="100"
                required
              ></b-form-textarea>
              <pre class="mt-2 mb-0"></pre>
            </b-col>
          </b-row>
          <br />
          <b-button type="submit" variant="dark" style="color:white;">
            완료
          </b-button>
        </b-form>
      </b-container>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      num: 0,
      n: {},
    };
  },
  methods: {
    update() {
      this.num = this.$route.params.num;
      console.log(this.n);
      axios
        .post('http://localhost:8080/happyhouse/notice/update', this.n)
        .then((res) => {
          console.log(res.data);
          this.n = res.data;
          this.$router.push('/happyhouse/notice/list');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.num = this.$route.params.num;
    axios
      .get(`http://localhost:8080/happyhouse/notice/detail/${this.num}`)
      .then((res) => {
        console.log(res.data);
        this.n = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style>
#make {
  display: inline-block;
}
#ta {
  width: 500px;
}
#input-default {
  width: 500px;
}
</style>
